<template>
<div>
	<swipe :slideList="slideList"></swipe>
	<content-mi :content="content"></content-mi>
</div>
</template>
<script>
	import swipea from '../../../static/mock/swipe.json'
	import content from '../../../static/mock/content.json'
import swipe from '../../base/swipe.vue'
import contentMi from '../../base/contentMi.vue'
export default {
	data(){
		return {
			slideList:swipea,
			content:content
		}
	},

	computed:{},
	components:{
		swipe,
		contentMi
	}
}
</script>
<style scoped>


</style>




