<template>
<div>
	
	<swipe :slideList="slideList"></swipe>
	<content-mi :content="content"></content-mi>
</div>
</template>
<script>
	import television from '../../../static/mock/home/Intelligence.json'
	import Intelligenceinfo from '../../../static/mock/home/Intelligenceinfo.json'
import swipe from '../../base/swipe.vue'
import contentMi from '../../base/contentMi.vue'
export default {
	data(){
		return {
			slideList:television,
			content:Intelligenceinfo
		}
	},

	computed:{},
	components:{
		swipe,
		contentMi
	}
}
</script>
<style scoped>


</style>




