<template>
<div>
	
	<swipe :slideList="slideList"></swipe>
	<content-mi :content="content"></content-mi>
</div>
</template>
<script>
	import Notebook from '../../../static/mock/home/Notebook.json'
	import Notebookinfo from '../../../static/mock/home/Notebookinfo.json'
import swipe from '../../base/swipe.vue'
import contentMi from '../../base/contentMi.vue'
export default {
	data(){
		return {
			slideList:Notebook,
			content:Notebookinfo
		}
	},

	computed:{},
	components:{
		swipe,
		contentMi
	}
}
</script>
<style scoped>


</style>




