<template>
	<div>
		<div class="ds">
			<div class="bgc" v-for="(item,index) in slideList" :key="index">
				<img v-lazy="item.img">
			</div>
		</div>
		
	</div>
</template>
<script>
	import television from '../../../static/mock/home/Intelligence.json'
export default {
	data(){
		return {
			slideList:television
		}
	},

	methods:{},
	computed:{},
	components:{}
}
</script>
<style scoped>
	.bgc {
		
		text-align: center;
		background-color: rgb(185, 79, 73);
	}
	img {
		width: 95%;
		margin: 5px;
	}
</style>


